import React from 'react';
import './styles.css';

import background from '../../assets/background.svg';

const Banner = () => {
  return (
    <section id="banner">
      <div className="banner-content">
        <img src={background} alt="Garoto com uma lupa"/>

        <div className="banner-description">
          <h1>Seja bem vindo ao Blog do Observador!</h1>
          <p>
            Aqui você encontrará diversas 
            notícias curiosas e inusitadas.
            Preparadas para você.
          </p>
        </div>
      </div>
    </section>
  );
}

export default Banner;
