import React from 'react';
import { FiTwitter, FiFacebook, FiCalendar } from 'react-icons/fi';
import { FaWhatsapp } from 'react-icons/fa';
import './styles.css';

import Header from '../../components/Header';
import Banner from '../../components/Banner';
import Aside from '../../components/Aside';

import SearchForm from '../../components/SearchForm';

const Detail = () => {
  return (
    <>
      <Header />
      <Banner />

      <section id="page-detail">
        <SearchForm />
        <div className="container">
          <Aside />

          <main>
            <article>
              <div className="header">
                <div className="social">
                  <button className="twitter">
                    <FiTwitter />
                  </button>
                  <button className="facebook">
                    <FiFacebook />
                  </button>
                  <button className="whatsapp">
                    <FaWhatsapp />
                  </button>
                </div>
                <div className="date">
                  <span>1 dia</span>
                  <div className="calendar">
                    <FiCalendar />
                  </div>
                </div>
              </div>
              <div className="content">
                <h1>
                  Programa “FIEG Mais Solidária” alega ter doado 100 toneladas de alimentos e 
                  insumos a famílias carentes: provavelmente não é verdade,
                  mas, se for, é uma mixaria que envergonha o grande empresariado 
                </h1>

                <div className="card-image">
                  <img src="https://www.blogdojlb.com.br/wp-content/uploads/2020/06/vitor-hugo-1.png" alt=""/>
                  <span>Legenda da imagem</span>
                </div>

                <p>
                  O presidente da Federação das Indústrias do Estado de Goiás – 
                  FIEG, o impagável Sandro Mabel, deitou falação em uma entrevista à 
                  rádio Sagres, onde pontificou sobre assuntos médicos e científicos 
                  (“O coronavírus veio para ficar. Não tem esse negócio de que nós vamos ficar 
                  livres do corona, não. O coronavírus vai pegar 70% das pessoas. De 60% a 80%, 
                  uma média de 70%, todas as pessoas vão pegar”, foi uma das suas declarações 
                  estapafúrdias), investiu mais uma vez contra o isolamento social como medida de 
                  prevenção contra a pandemia e se gabou de liderar um programa, denominado 
                  “FIEG Mais Solidária”, que já teria doado 100 toneladas de alimentos e insumos 
                  à população carente do Estado.
                  2) Lincoln Tejota pode andar barbado e desgrenhado? Pode. Mas um vice-governador não deve e não pode. É péssimo exemplo o que ele está dando, ao continuar circulando e se apresentando com a barba que usa já há alguns anos. Trata-se de um ornamento de pelos que dificulta a higienização do rosto, principalmente fora de casa, e não permite uma vedação adequada quando se usa máscara de proteção (expondo a riscos a saúde de pessoas próximas). Novamente, confiram na foto acima, leitora e leitor, que na verdade é um print de tela da live que Lincoln Tejota com jornalistas de O Popular. Não é uma imagem boa. O presidente do Supremo Tribunal Federal Dias Toffoli tirou o cavanhaque e o bigode logo nos primeiros dias da pandemia. O ex-ministro e secretário de Transportes de São Paulo Alexandre Baldy também se despediu da sua barba igualmente no começo da emergência sanitária que o país passou a viver. Há dezenas de outras autoridades que fizeram o mesmo, por uma questão mínima de bom senso e juízo. O nosso vice precisa se antenar.
                </p>
              </div>
            </article>
          </main>
        </div>
      </section>
    </>
  );
}

export default Detail;
