import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import { FiFilePlus, FiDelete, FiEdit } from 'react-icons/fi';
import './styles.css';
import api from '../../services/api';

import SearchForm from '../SearchForm';
import FormPost from '../FormPost';

import img from '../../assets/default.webp';

interface PostApi {
  id_noticia: number,
  titulo_noticia: string,
  descriacao_noticia: string,
  data_noticia: number,
  prioridade_noticia: number,
  url_imagem: string,
  lengenda: string
};

const AdmPost = () => {
  const [posts, setPosts] = useState<PostApi[]>([]);

  const [formPost, setFormPost] = useState(false);

  useEffect(() => {
    api.get('listar-noticias').then(response => {
      setPosts(response.data);
    });
  }, []);

  function toggleVisibleForm(status: boolean) {
    setFormPost(status);
  }

  async function handleDelete(id: number) {
    const currentPost = posts.findIndex(item => item.id_noticia === id);

    if (!currentPost) return;

    await api.delete(`delete-noticia/${id}`);

    const filteredPosts = posts.filter(post => post.id_noticia !== id);

    setPosts(filteredPosts);
  }

  return (
    <div id="show-post" className={formPost ? 'hide' : ''}>
      <h1>Todos os Posts</h1>

      <div className="actions">
        <SearchForm />
        <button onClick={() => toggleVisibleForm(true)}>
          Escrever
          <FiFilePlus />
        </button>
      </div>

      {formPost && <FormPost toggleVisibleForm={toggleVisibleForm} />}

      <ul id="list-post">

        {posts.map(post => (
          <li key={post.id_noticia}>
            <div className="post-header">
              <button className="edite">
                <FiEdit />
              </button>
              <button 
                onClick={() => handleDelete(post.id_noticia)}
                className="delete"
              >
                <FiDelete />
              </button>
            </div>
            <div className="content">
              <img src={post.url_imagem ? post.url_imagem : img} alt="imagem default"/>

              <Link className="content-title" to="/detalhe">
                {post.titulo_noticia}
              </Link>
            </div>
          </li>
        ))}

      </ul>
    </div>
  );
}

export default AdmPost;
