import React, { useState, useEffect, ChangeEvent, FormEvent } from 'react';
import './styles.css';
import api from '../../services/api';

interface Config {
  id: number,
  title: string,
  detail: string
};

const ConfigPage = () => {
  const [page, setPage] = useState<Config>({
    id: 0,
    title: '',
    detail: ''
  });

  useEffect(() => {
    api.get('detalhe-blog').then(response => {
      const { data } = response;

      try {
        if (data[0].id) {
          
          setPage({
            id: data[0].id,
            title: data[0].nome_blog,
            detail: data[0].descricao_blog
          })
        }
      } catch (Exeption) {
        console.log('Não foi possível obter os dados do servidor');
      }
    });
  }, []);

  function handleChangeInput(event: ChangeEvent<HTMLInputElement>) {
    const { name, value } = event.target;

    setPage({ ...page, [name]: value });
  }

  function handleSubmit(event: FormEvent) {
    event.preventDefault();

    if (!page.title && !page.detail) {
      alert('Estes campos são obrigatórios');
    }

    api.post('editar-blog', {
      nome_blog: page.title,
      descricao_blog: page.detail
    })
      .then(reaponse => {
        alert('Os dados foram salvos');
      })
      .catch(error => {
        alert('Oops, falha na conexao com servidor!');
      });
  }

  return (
    <div className="data-page">
      <h1>Configurações da página</h1>

      <form onSubmit={handleSubmit}>

        <div className="input-control">
          <label htmlFor="title">Titulo do banner</label>
          <input 
            id="title"
            type="text"
            name="title"
            value={page.title}
            onChange={handleChangeInput}
          />
        </div>

        <div className="input-control">
          <label htmlFor="detail">Descrição do banner</label>
          <input 
            id="detail"
            type="text"
            name="detail"
            value={page.detail}
            onChange={handleChangeInput}
          />
        </div>

        <button type="submit">Salvar</button>
      </form>
    </div>
  );
}

export default ConfigPage;
