import React from 'react';
import { FiSearch } from 'react-icons/fi';

import './styles.css';

const SearchForm = () => {
  return (
    <form className="search-form">
      <input type="text" placeholder="Procurando algo?" />
      <button type="submit">
        <FiSearch />
      </button>
    </form>
  );
}

export default SearchForm;
