import React, { useState, FormEvent } from 'react';
import { FiX } from 'react-icons/fi';
import './styles.css';
import api from '../../services/api';

import Dropzone from '../Dropzone';

interface PostFormProps {
  toggleVisibleForm: (status: boolean) => void
};

const FormPost: React.FC<PostFormProps> = ({ toggleVisibleForm }) => {
  const [title, setTitle] = useState('');
  const [description, setDescription] = useState('');
  const [priority, setPriority] = useState(0);
  const [legend, setLegend] = useState('');

  const [selectedFile, setSelectedFile] = useState<File>();

  async function handleSubmit(event: FormEvent) {
    event.preventDefault();

    const titulo_noticia = title;
    const descricao_noticia = description.split('\n').join('<br/>');
    const data_noticia = new Date().getTime();

    const data = new FormData();

    data.append('titulo_noticia', titulo_noticia);
    data.append('descricao_noticia', descricao_noticia);
    data.append('data_noticia', String(data_noticia));
    data.append('prioridade_noticia', String(priority));

    if (selectedFile) {
      data.append('url_imagem', selectedFile);
      data.append('legenda_imagem', legend);
    }

    await api.post('cadastrar-noticia', data);

    alert('Post cadastrado com sucesso!');
  }

  function handleClickPriority() {
    priority ? setPriority(0) : setPriority(1);
  }

  return (
    <div className="form-post">
      <div className="content">
        <div className="content-header">
          <h1>Post</h1>
          <button onClick={() => toggleVisibleForm(false)}>
            <FiX />
          </button>
        </div>
        
        <form onSubmit={handleSubmit}>

          <Dropzone onFileUploaded={setSelectedFile} />
          
          <div className="input-control title">
            <label htmlFor="legend">Legenda para imagem</label>
            <input 
              id="legend"
              value={legend}
              onChange={event => setLegend(event.target.value)}
              type="text"
              required
              disabled={selectedFile ? false : true}
            />
          </div>

          <div className="input-control">
            <label htmlFor="title">Título</label>
            <input 
              id="title"
              value={title}
              onChange={event => setTitle(event.target.value)}
              type="text"
              required
            />
          </div>

          <div className="input-control">
            <label htmlFor="description">Descrição</label>
            <textarea
              id="description"
              value={description}
              onChange={event => setDescription(event.target.value)}
              cols={30} 
              rows={20}
              required
            >
            </textarea>
          </div>

          <div className="input-control check">
            <label htmlFor="priority">
              <input 
                type="checkbox"
                value={priority}
                onChange={handleClickPriority} 
                name="priority" 
                id="priority"
              />
              Destacar post
            </label>
          </div>

         <button type="submit">Salvar</button>
        </form>
      </div>
    </div>
  );
}

export default FormPost;
