import React, { useState, ChangeEvent, FormEvent } from 'react';
import { useHistory } from 'react-router-dom';
import { FiSearch } from 'react-icons/fi';
import './styles.css';

import api from '../../services/api';

import background from '../../assets/background.svg';

interface User {
  email: string,
  password: string
}

const Login = () => {
  const [user, setUser] = useState<User>({} as User);

  const navigation = useHistory();

  function handleChangeInput(event: ChangeEvent<HTMLInputElement>) {
    const { name, value } = event.target;

    setUser({ ...user, [name]: value });
  }

  function handleSubmit(event: FormEvent) {
    event.preventDefault();

    if (!user.email || !user.password) return;

    api.post('login', {
      email: user.email,
      senha: user.password
    })
      .then(response => {
        try {

          const id = response.data[0].id_admin;
    
          id && navigation.push('/painel-controle', 'ok');
          
        } catch (Exeption) {
          alert('Oops, não foi possível logar');
        }
      })
      .catch(error => {
        alert('Oops, falha ao logar verifique sua conexão');

        //navigation.push('/painel-controle', 'ok');
      });

  }

  return (
    <div id="page-login">
      <div className="login-container">
        <main>
          <h1>
            <FiSearch />
            Blog do Observador
          </h1>

          <form onSubmit={handleSubmit}>
            <h2>Entre com seus dados</h2>

            <input
              type="email" 
              placeholder="E-mail"
              name="email"
              onChange={handleChangeInput}
              required
            />
            <input 
              type="password" 
              placeholder="Senha"
              name="password"
              onChange={handleChangeInput}
              required
            />
            <button type="submit">Entrar</button>
          </form>
          
        </main>
        <div className="background">
          <img src={background} alt="demo"/>
        </div>
      </div>
    </div>
  );
}

export default Login;
