import React, { useState } from 'react';
import { FiSearch, FiLogOut, FiHome, FiSettings } from 'react-icons/fi';
import { useHistory } from 'react-router-dom';
import './styles.css';

import background from '../../assets/background.svg';

import AdmPost from '../../components/AdmPost';
import ConfigPage from '../../components/ConfigPage';

const ControlPanel = () => {
  const [active, setActive] = useState('DASK');
  
  const navigation = useHistory();

  const { state } = navigation.location;

  if (state !== 'ok') {
    navigation.push('/');
  }

  function logout() {
    navigation.push('/login');
  }

  return (
    <div id="control-panel">
      <aside>
        <nav>
          <div className="nav-header">
            <img src={background} alt="logo"/>
            <div className="name-blog">
              <FiSearch />
              Blog do Observador
            </div>
          </div>
          <ul>
            <li>
              <button 
                className={active === 'DASK' ? 'active' : ''} 
                onClick={() => setActive('DASK')}
              >
                <FiHome />
                Daskbord
              </button>
            </li>
            <li>
              <button 
                className={active === 'CONF' ? 'active' : ''} 
                onClick={() => setActive('CONF')}
              >
                <FiSettings />
                Configuração
              </button>
            </li>
            <li>
              <button  
                onClick={logout}
              >
                <FiLogOut />
                Sair
              </button>
            </li>
          </ul>
        </nav>
      </aside>
      <main>
        { active === 'DASK' && <AdmPost /> }
        { active === 'CONF' && <ConfigPage /> }
      </main>
    </div>
  );
}

export default ControlPanel;
